<template>
    <div class="left">
        <ul class="left-ul">
            <li class="left-li-title">开发指南</li>
            <li class="left-li"><router-link :to="{name:'index'}" tag="span">介绍</router-link></li>
            <li class="left-li"><router-link :to="{name:'quickStart'}" tag="span">快速上手</router-link></li>
            <li class="left-li"><router-link :to="{name:'codeStandard'}" tag="span">编码规范</router-link></li>
            <li class="left-li-title">基础组件</li>
            <li class="left-li"><router-link :to="{name:'button'}" tag="span">按钮</router-link></li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: 'Left',
        data() {
            return {

            }
        }
    }
</script>
<style scoped>
    .left{width: 100%;overflow: hidden;}
    .left-ul{display: block;overflow: hidden;padding-top:30px;}
    .left-li-title{height: 44px;line-height: 44px;display: block;overflow: hidden;color:#455a64;font-size: 14px;
        font-weight: 600;text-indent: 30px;}
    .left-li{height: 44px;line-height: 44px;display: block;overflow: hidden;color:#455a64;font-size: 14px;text-indent: 30px;
        cursor: pointer;
    }
    .left-li span{display: block;width: 100%;height: 100%;}

    .left-li span.router-link-active{color:#4fc08d;}
</style>



